@import "../global.scss";

.navbar {
  width: 100%;
  height: 70px;
  background-color: $primaryColor;
  color: $mainColor;
  position: fixed;
  top: 0px;
  z-index: 3;
  transition: all 1s ease;

  .container {
    padding: 10px 30px 10px 30px;
    display: flex;
    align-items: center;
    justify-content: space-between;

    .left {
      display: flex;
      align-items: center;

      .logo {
        margin-right: 40px;

        .jsLogo {
          height: 40px;
          border-radius: 8px;
          border: 2px solid $mainColor;
        }
      }

      .itemContainer {
        display: flex;
        align-items: center;
        margin-left: 30px;

        .icon {
          font-size: 18px;
          margin-right: 5px;
        }

        span {
          font-size: 15px;
          font-weight: 400;
        }
      }
    }

    .right {
      .hamburgerMenu {
        width: 32px;
        height: 25px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        cursor: pointer;

        span {
          width: 100%;
          height: 3px;
          background-color: $mainColor;
          transform-origin: left;
          transition: all 1s ease;
        }
      }
    }
  }

  &.active {
    background-color: $mainColor;
    color: $primaryColor;

    .hamburgerMenu {
      span {
        &:first-child {
          background-color: $primaryColor;
          transform: rotate(45deg);
        }

        &:nth-child(2) {
          opacity: 0;
        }

        &:last-child {
          background-color: $primaryColor;
          transform: rotate(-45deg);
        }
      }
    }
  }
}
